import { actionFactory, getState, dispatch, persist } from '../../engine'
import { List } from 'immutable-sorted'
import MenuTabs from '../Menu/MenuTabs'

import { decode } from '../../utils/permaLink'

// This function is called after Duxen creates an engine,
// creates and initial state (restores the saved state)
const AppInit = (engine, store, initState) => {
  const state = getState()
  const urlArgs = state.get("args")
  const code = urlArgs.get("code")
  const tab = urlArgs.get("tab")
  const args = urlArgs.get("args")
  const reset = urlArgs.get("reset")

  if(reset === "true") {
    if(window.confirm("This will reset all saved data in JellyBalls. Are you sure?")) {
      dispatch(actionFactory.init(initState))
    }
    window.history.replaceState({}, document.title, "/");
    return
  }

  try {
    if(tab) {
      for(let i = 0; i < MenuTabs.length; i++) {
        if(tab === MenuTabs[i]) {
          dispatch(actionFactory.value("selectedTabIndex", i))
        }
      }
    }

    if(code) {
      dispatch(actionFactory.value("home.link", decode(code)))
    } else {
      persist()
    }

    if(args) {
      const value = List(JSON.parse(decode(args)))
      dispatch(actionFactory.value("home.args", value))
    }
  } catch(err) {
    console.log("INIT ERROR:", err)
  }
}

export default AppInit
