import React, { Component } from 'react'

import InputLayout from '../components/InputLayout'
import InputLayout2 from '../components/InputLayout2'
import InputArg from '../components/InputArg'
import Chain from '../components/Chain'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { main } from '../../jellylanguage/interpreter'

import '../Table/Table.css'

const style = "minimalistBlack"

export default class RunPage extends Component {

  constructor(props) {
    super(props)
    this.state = {args: [], output: "Run it already", traces: []}
  }

  componentDidMount() {
    this.runit()
  }

  componentDidUpdate(prevProps) {
    if(prevProps.link !== this.props.link) {
      // this.runit()
    }
  }

  getArg(index) {
    const props = this.props
    let argStr = props.args.get(index)
    if(argStr === undefined || argStr === null) {
      return null
    }

    try {
      return JSON.parse(argStr)
    } catch(err) {

    }
    return argStr
  }

  runit() {
    const props = this.props
    const traces = []
    const args = [
      this.getArg(0),
      this.getArg(1),
      this.getArg(2),
      this.getArg(3),
      this.getArg(4),
    ]
    const output = main(props.link, args, '', traces)
    this.setState({args, output, traces})
  }

  click(e) {
    this.runit()
  }

  render() {
    const props = this.props
    let i = 1
    
    return <div>
      <h3>Run</h3>
      <InputLayout {...props}/>
      <InputLayout2 {...props}/>
      <h3>Arguments</h3>
      <InputArg {...props} label="³" index={0} change={props.setArg}/>
      <InputArg {...props} label="⁴" index={1} change={props.setArg}/>
      <InputArg {...props} label="⁵" index={2} change={props.setArg}/>
      <InputArg {...props} label="⁶" index={3} change={props.setArg}/>
      <InputArg {...props} label="⁷" index={4} change={props.setArg}/>
      <h3>
        <button 
          name="Run"
          title="Run"
          alt="Run"
          onClick={this.click.bind(this)}
        ><FontAwesomeIcon icon="running"/>&nbsp;Run!</button>
      </h3>
      <h3>Output</h3>
      <h3>
        {this.state.output}
      </h3>
      <h3>Trace</h3>
      <table className={style}>
        <thead>
          <tr>
          <th>Action</th>
          <th><center>Code</center></th>
          <th><center>Args</center></th>
          <th><center>Result</center></th>
          </tr>
        </thead>
        <tbody>
          {this.state.traces.map(t=>
          <tr key={i++} className={t.action.endsWith("_chain")||t.action === "jelly_eval"?"chain":""}>
          <td>{t.action}</td>
          <td><center><Chain defaultTitle=" " chain={t.chain || t.link || t.code || t.result}/></center></td>
          <td><center>{t.args?(t.args.length<1 || t.args[0] === null?"":JSON.stringify(t.args[0]))+(t.args.length<2 || t.args[1] === null?"":", "+JSON.stringify(t.args[1])):JSON.stringify(t.arg)}</center></td>
          <td><center>{t.result}</center></td>
          </tr>
          )}
        </tbody>
      </table>
      <br/>
    </div>
  }
}
