function textToByteString(string) {
	return unescape(encodeURIComponent(string));
}

function byteStringToText(byteString) {
	return decodeURIComponent(escape(byteString));
}

function byteStringToBase64(byteString) {
	return btoa(byteString).replace(/\+/g, "@").replace(/=+/, "");
}

function base64ToByteString(base64String) {
	return atob(unescape(base64String).replace(/@|-/g, "+").replace(/_/g, "/"))
}

export const encode = (code) => byteStringToBase64(textToByteString(code))
export const decode = (code) => byteStringToText(base64ToByteString(code))

// URL Example
// https://tio.run/#jelly#code=cHJpbnRsbih4KQ&footer=cHJpbnRsbih4KQ&args=cHJpbnRsbih4KQ+cHJpbnRsbih4KQ+cHJpbnRsbih4KQ+cHJpbnRsbih4KQ+cHJpbnRsbih4KQ
export const tioPermaLink = (code, args) => {
  var s = ""
  s += "https://tio.run"
  s += "/#jelly#"
  s += "code="
  s += byteStringToBase64(textToByteString(code))
  let n = 0
  if(args) {
    for(let i = 0; i < args.length; i++) {
      const arg = args[i]
      if(arg !== null) {
        s += (n===0?"&args=":"+")
        n++
        s += byteStringToBase64(textToByteString(arg.toString()))
      }
    }
  }
  return s
}

export const jbPermaLink = (code, menuTab, args) => {
  var s = ""
  // s += "https://jellyballs.github.io"
  // s += "localhost:3001"
  s += "/?"
  s += "tab="+menuTab
  s += "&code="
  s += byteStringToBase64(textToByteString(code))
  s += "&args="
  s += byteStringToBase64(textToByteString(JSON.stringify(args)))
  return s
}
