const chain_separators = {
	'ø': [0, '', true],
	'µ': [1, '', true],
	')': [1, '€', true],
	'ð': [2, '', true],
	'ɓ': [2, '', false]
}

const default_chain_separation = [-1, '', true]

const str_arities = Object.keys(chain_separators).join('')
const str_strings = '“[^«»‘’”]*[«»‘’”]?'
const str_charlit = '”.'
const str_chrpair = '⁾..'
const str_intpair = '⁽..'
const str_realdec = '(0|-?\\d*\\.\\d*|-?\\d+|-)'
const str_realnum = ['(', '?ȷ', '?|', ')'].join(str_realdec)
const str_complex = ['(', '?ı', '?|', ')'].join(str_realnum)
const str_literal = '('+[str_strings, str_charlit, str_chrpair, str_complex, str_intpair].join('|')+')'
const str_litlist = '\\[*' + str_literal + '((\\]*,\\[*)' + str_literal + ')*\\]*'

const  escapeRegExp = (string) => {
  return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
}

const as = Object.keys(atoms)
const qs = Object.keys(quicks).filter(k=>quicks[k].type === "quick" || quicks[k].type === "Ðquick")
const str_nonlits = as.concat(qs).map(escapeRegExp).join('|')

export const regex_chain = new RegExp('(^(' + str_nonlits + '|' + str_litlist + '| )+|[' + str_arities + '])(' + str_nonlits + '|' + str_litlist + '| )*', 'g')
export const regex_liter = new RegExp(str_litlist, 'g')
export const regex_token = new RegExp(str_nonlits + '|' + str_litlist, 'g')
export const regex_flink = new RegExp('([' + str_arities + ']|' + str_nonlits + '|' + str_litlist + ')', 'g')

export const findAll = (rx, s) => {
  var matches = [];
  var match
  while((match = rx.exec(s)) !== null) {
     matches.push(match[0]);
  }
  return matches
}

export const parseChain = (chain) => {
  return findAll(regex_chain, chain)
}

export const parseBalls = (chain) => {
  return findAll(regex_flink, chain)
}

export const parseLiter = (litlist) => {
  return findAll(regex_liter, litlist)
}

function create_chain(chain, arity = -1, isForward = true) {
	return {
    arity: arity,
    key: "chain",
		chain: chain,
    call: "lambda x = None, y = None: variadic_chain(chain, isForward and (x, y) or (y, x))",
    js: (x=undefined, y=undefined) => variadic_chain(chain, isForward ? [x, y] : [y, x])
  }
}

function create_literal(s) {
	return {
    arity: 0,
    key: 'literal',
    call: "lambda: python_eval(string, False)",
    js: () => JSON.parse(s)
  }
}

export function parse_code(code) {
	const lines = code.split("¶")
  const links = []
  lines.forEach(line=>links.push([]))

  for(let index = 0; index < lines.length; index++) {
    const line = lines[index]
		const chains = links[index]
    const words = findAll(regex_chain, line)
    words.forEach(word => {
      const chain = []
      let sep = chain_separators[word[0]] || default_chain_separation
      const arity = sep[0]
      const start = sep[1]
      const isForward = sep[2]
      findAll(regex_token, start + word).forEach(token => {
        console.log("TOKEN", token)
				if(atoms.hasOwnProperty(token)) {
					chain.push(atoms[token])
        } else if(quicks.hasOwnProperty(token)) {
					// const popped = []
					// while not quicks[token].condition(popped) and (chain or chains):
					// 	popped.insert(0, chain.pop() if chain else chains.pop())
					// chain += quicks[token].quicklink(popped, links, index)
        // } else if token in hypers:
          // 	x = chain.pop() if chain else chains.pop()
          // 	chain.append(hypers[token](x, links))
        } else {
          // chain.append(create_literal(regex_liter.sub(parse_literal, token)))
          chain.push(create_literal(token))
        }
      })
      console.log("CHAIN", chain)
      chains.push(create_chain(chain, arity, isForward))
    })
  }
	return links
}
