import React, { Component } from 'react'

import css from './InputArg.module.css'

export default class InputArg extends Component {

  change(e) {
    const props = this.props
    const index = props.index
    const value = e.target.value
    props.change(index, value===""?null:value)
  }

  render() {
    const props = this.props
    const index = props.index
    const arg = props.args.get(index)

    return <div>
      <label>{props.label}&nbsp;</label>
      <input 
        className={css.input}
        type="text" 
        name={"InputArg"+index} 
        title={"Arg"+index}
        placeholder={"arg"+index}
        value={arg===null?"":arg}
        onChange={this.change.bind(this)}
      />
      </div>
  }

}
