import React from 'react'

import Tio from './Tio'
import Jb from './Jb'

const InputLayout = (props) => {
  const chain = props.link

  return <div>
    <h3>
      <Jb short={true} chain={chain} selectedTabIndex={props.selectedTabIndex} args={props.args}/>
      &nbsp;&nbsp;&bull;&nbsp;&nbsp;
      {chain.length}&nbsp;B
      &nbsp;&nbsp;&bull;&nbsp;&nbsp;
      <Tio short={true} chain={chain} args={props.args}/>
    </h3>
  </div>
}

export default InputLayout
